  height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  background-color: rgba(255,255,255, 0.8);
  width: 400px;
  margin: 50px auto;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
}

.container h1{
  color: var(--primary-color);
}

.container p{
  padding: 25px 0;
}

